"use client";

import React, { useMemo, useState } from "react";
import PageBreadcrumb from "@/components/common/PageBreadCrumb";
import { ChevronLeft, ChevronRight } from "lucide-react";

type DayType = "WORKING" | "LEAVE" | "HOLIDAY";

interface DayEntry {
    hours: number;
    overtime: number;
    project: string;
    notes: string;
    type: DayType;
}

const PROJECTS = ["None", "Project Alpha", "Project Beta", "Internal"];

const DEFAULT_ENTRY: DayEntry = {
    hours: 8,
    overtime: 0,
    project: "None",
    notes: "",
    type: "WORKING",
};

/* Sunday-based week */
function startOfWeek(date: Date) {
    const d = new Date(date);
    d.setDate(d.getDate() - d.getDay());
    d.setHours(0, 0, 0, 0);
    return d;
}

function addDays(date: Date, days: number) {
    const d = new Date(date);
    d.setDate(d.getDate() + days);
    return d;
}

export default function WeeklyTimesheetCalendar() {
    const todayWeek = startOfWeek(new Date());
    const [currentWeek, setCurrentWeek] = useState(todayWeek);
    const [entries, setEntries] = useState<Record<string, DayEntry>>({});

    const weekDays = useMemo(() => {
        return Array.from({ length: 7 }).map((_, i) => {
            const date = addDays(currentWeek, i);
            const key = date.toISOString().split("T")[0];
            return {
                date,
                key,
                entry: { ...DEFAULT_ENTRY, ...entries[key] }, // ðŸ”’ FIXED
            };
        });
    }, [currentWeek, entries]);

    const updateEntry = (key: string, updates: Partial<DayEntry>) => {
        setEntries(prev => ({
            ...prev,
            [key]: { ...DEFAULT_ENTRY, ...prev[key], ...updates },
        }));
    };

    const canGoNextWeek = currentWeek < todayWeek;

    return (
        <div className="p-6 space-y-6">
            <PageBreadcrumb pageTitle="Weekly Timesheet" />

            {/* Header */}
            <div className="flex items-center justify-between">
                <div className="flex items-center gap-3">
                    <button
                        onClick={() => setCurrentWeek(addDays(currentWeek, -7))}
                        className="p-2 rounded hover:bg-gray-100"
                    >
                        <ChevronLeft />
                    </button>

                    <h2 className="font-semibold text-lg">
                        {currentWeek.toDateString()} â€“ {addDays(currentWeek, 6).toDateString()}
                    </h2>

                    <button
                        disabled={!canGoNextWeek}
                        onClick={() => setCurrentWeek(addDays(currentWeek, 7))}
                        className={`p-2 rounded ${canGoNextWeek ? "hover:bg-gray-100" : "opacity-40 cursor-not-allowed"
                            }`}
                    >
                        <ChevronRight />
                    </button>
                </div>
            </div>

            {/* Calendar Grid */}
            <div className="overflow-x-auto border rounded-xl bg-white shadow">
                <table className="w-full border-collapse">
                    <thead className="bg-gray-50">
                        <tr>
                            {weekDays.map(d => (
                                <th key={d.key} className="border p-3 text-sm font-semibold">
                                    {d.date.toLocaleDateString("default", { weekday: "short" })}
                                    <div className="text-xs text-gray-500">
                                        {d.date.getDate()}
                                    </div>
                                </th>
                            ))}
                        </tr>
                    </thead>

                    <tbody>
                        {/* Type */}
                        <tr>
                            {weekDays.map(d => (
                                <td key={d.key} className="border p-2">
                                    <select
                                        className="w-full border rounded px-2 py-1 text-sm"
                                        value={d.entry.type}
                                        onChange={e =>
                                            updateEntry(d.key, { type: e.target.value as DayType })
                                        }
                                    >
                                        <option value="WORKING">Working</option>
                                        <option value="LEAVE">Leave</option>
                                        <option value="HOLIDAY">Holiday</option>
                                    </select>
                                </td>
                            ))}
                        </tr>

                        {/* Project */}
                        <tr>
                            {weekDays.map(d => (
                                <td key={d.key} className="border p-2">
                                    <select
                                        disabled={d.entry.type !== "WORKING"}
                                        className="w-full border rounded px-2 py-1 text-sm"
                                        value={d.entry.project}
                                        onChange={e => updateEntry(d.key, { project: e.target.value })}
                                    >
                                        {PROJECTS.map(p => (
                                            <option key={p}>{p}</option>
                                        ))}
                                    </select>
                                </td>
                            ))}
                        </tr>

                        {/* Hours */}
                        <tr>
                            {weekDays.map(d => (
                                <td key={d.key} className="border p-2">
                                    <input
                                        type="number"
                                        className="w-full border rounded px-2 py-1 text-sm"
                                        value={d.entry.hours}
                                        disabled={d.entry.type !== "WORKING"}
                                        onChange={e =>
                                            updateEntry(d.key, { hours: Number(e.target.value) })
                                        }
                                    />
                                </td>
                            ))}
                        </tr>

                        {/* Overtime */}
                        <tr>
                            {weekDays.map(d => (
                                <td key={d.key} className="border p-2">
                                    <input
                                        type="number"
                                        className="w-full border rounded px-2 py-1 text-sm"
                                        value={d.entry.overtime}
                                        disabled={d.entry.type !== "WORKING"}
                                        onChange={e =>
                                            updateEntry(d.key, { overtime: Number(e.target.value) })
                                        }
                                    />
                                </td>
                            ))}
                        </tr>

                        {/* Notes */}
                        <tr>
                            {weekDays.map(d => (
                                <td key={d.key} className="border p-2">
                                    <input
                                        type="text"
                                        className="w-full border rounded px-2 py-1 text-sm"
                                        value={d.entry.notes}
                                        disabled={d.entry.type !== "WORKING"}
                                        onChange={e =>
                                            updateEntry(d.key, { notes: e.target.value })
                                        }
                                    />
                                </td>
                            ))}
                        </tr>
                    </tbody>
                </table>
            </div>

            {/* Actions */}
            <div className="flex justify-end gap-3">
                <button className="px-5 py-2 border rounded hover:bg-gray-100">
                    Save Draft
                </button>
                <button className="px-5 py-2 bg-blue-600 text-white rounded hover:bg-blue-700">
                    Submit Week
                </button>
            </div>
        </div>
    );
}
